import * as THREE from 'three'
import vert from './vert'
import frag from './frag'

let camera, scene, renderer, material, mesh

const uniforms = {
  iGlobalTime: { type: 'f', value: 0 },
  iResolution: { type: 'v2', value: new THREE.Vector2() },
  iMouseCoord: { type: 'v2', value: new THREE.Vector2() },
}

const size = [window.innerWidth, window.innerHeight]

uniforms.iResolution.value.x = size[0]
uniforms.iResolution.value.y = size[1]

init()
animate()

function init() {
  camera = new THREE.PerspectiveCamera(120, size[0] / size[1], 1, 5000)
  camera.position.z = 1000
  scene = new THREE.Scene()
  scene.background = new THREE.Color(0x333333)

  const geometry = new THREE.CircleBufferGeometry(1400, 60)
  material = new THREE.ShaderMaterial({
    fragmentShader: frag,
    vertexShader: vert,
    uniforms,
    extensions: { derivatives: true },
  })

  mesh = new THREE.Mesh(geometry, material)
  scene.add(mesh)

  renderer = new THREE.WebGLRenderer()
  renderer.setPixelRatio(window.devicePixelRatio)
  renderer.setSize(window.innerWidth, window.innerHeight)
  document.body.appendChild(renderer.domElement)

  document.addEventListener('resize', onWindowResize, false)
  document.addEventListener('mousemove', onMousemove, false)
  document.addEventListener('touchstart', onTouchmove, { passive: false })
  document.addEventListener('touchmove', onTouchmove, { passive: false })
}

function onMousemove(e) {
  const vec = new THREE.Vector3(
    e.clientX / window.innerWidth * 2 - 1,
    -(e.clientY / window.innerHeight) * 2 + 1,
    0.5,
  ).unproject(camera)

  const dir = vec.sub(camera.position).normalize()
  const dst = -camera.position.z / dir.z
  const pos = camera.position.clone().add(dir.multiplyScalar(dst))

  uniforms.iMouseCoord.value.x = pos.x
  uniforms.iMouseCoord.value.y = pos.y
}

function onTouchmove(e) {
  e.stopPropagation()
  e.preventDefault()

  if (!e.touches || !e.touches[0]) {
    return
  }

  const vec = new THREE.Vector3(
    e.touches[0].clientX / window.innerWidth * 2 - 1,
    -(e.touches[0].clientY / window.innerHeight) * 2 + 1,
    0.5,
  ).unproject(camera)

  const dir = vec.sub(camera.position).normalize()
  const dst = -camera.position.z / dir.z
  const pos = camera.position.clone().add(dir.multiplyScalar(dst))

  uniforms.iMouseCoord.value.x = pos.x
  uniforms.iMouseCoord.value.y = pos.y
}

function onWindowResize() {
  camera.aspect = window.innerWidth / window.innerHeight
  camera.updateProjectionMatrix()
  renderer.setSize(window.innerWidth, window.innerHeight)
}

const start = Date.now()

function animate() {
  requestAnimationFrame(animate)

  const elapsedMilliseconds = Date.now() - start
  const elapsedSeconds = elapsedMilliseconds / 1000
  uniforms.iGlobalTime.value = 5 * elapsedSeconds

  renderer.render(scene, camera)
}



